section.pricing {
  background: #007bff;
  background: linear-gradient(to right, #0062E6, #33AEFF);
}


.pricing hr {
  margin: 1.5rem 0;
}


.pricing ul li {
  margin-bottom: 1rem;
}

.container{
  display: flex;
  flex-direction: column;
  margin-right: 40px;
  margin-left: 40px;
  flex-wrap: wrap;
  
}

.sub2,.sub1,.sub3{
  margin: 30px;
  background-color: white;
  border-radius: 1rem;
}
.subtitle{
  text-align: center;
  color: gray;
  letter-spacing: 1mm;
}
.subprice{
  text-align: center;
  font-size: 60px;
  margin: 0px;
  font-weight:500;
}
.period{
  font-size: 15px;
  font-weight: lighter;
}
.s15,.s16,.s17,.s18,.s28{
  color: gray;
}
ul{
  list-style-type:none;
}
.block {
  display: block;
  width: 90%;
  border: none;
  background-color: #6785e6;
  padding: 14px 28px;
  font-size: 12px;
  cursor: pointer;
  text-align: center;
  margin-right: 120px;
  border-radius: 2rem;
  margin-left: 60px;
  margin-bottom: 10px;
  color: white;
  letter-spacing: 1mm;
  font-weight: bolder;
}
.block:hover {
  background-color: #486eeb;
}